// ** React Imports
import { useState } from 'react'
import { useDispatch, useSelector } from "react-redux"
import { Link, useHistory } from 'react-router-dom'

// ** Custom Components
import UserViewAvatar from '@components/user-avatar/UserViewAvatar'
import Avatar from '@components/avatar'

// ** Third Party Components
import { Button, Card, CardBody, CardText, Col, Media, Modal, ModalBody, ModalFooter, ModalHeader, Row } from 'reactstrap'
import { Calendar, Check, Feather, Phone, User } from 'react-feather'

// ** Store Actions
import { deactivateVendorEmployee, deleteVendorEmployee } from '@store/Vendors/actions'

import { DATE_FORMATS, formatDate, formatPhone } from '@helper'

const EmployeeInfoCard = () => {
  const history = useHistory()
  const [disabledModal, setDisabledModal] = useState(false)
  const [modalTitle, setModalTitle] = useState('')
  const [modalDesc, setModalDesc] = useState('')
  const [action, setAction] = useState('')
  const { selectedVendorEmployee } = useSelector(state => state.vendors),
    dispatch = useDispatch()

  const { rolesConfig } = useSelector(state => state.roles)
  const { roles, status } = rolesConfig.vendorEmployee

  const handleConfirm = () => {
    const { _id, vendorId } = selectedVendorEmployee
    setDisabledModal(false)
    if (action === 'deactivate') {
      dispatch(deactivateVendorEmployee(_id, vendorId))
    } else if (action === 'delete') {
      dispatch(deleteVendorEmployee(_id, vendorId))
      history.push(`/vendors/view/${vendorId}`)
    }
  }

  return (
    <>
      <Card>
        <CardBody>
          <Row>
            <Col xl='4' lg='12' className='d-flex flex-column justify-content-between border-container-lg'>
              <UserViewAvatar
                name={`${selectedVendorEmployee.firstName} ${selectedVendorEmployee.lastName}`}
                email={selectedVendorEmployee.email}
                photo={selectedVendorEmployee.profilePictureURL}
              />
            </Col>
            <Col xl='3' lg='12' className='mt-2 mt-xl-0'>
              <div className='user-info-wrapper'>
                <Media className='mt-2'>
                  <Avatar color='light-primary' className='rounded mr-1' icon={<User size={18} />} />
                  <Media body>
                    <h6 className='mb-0'>Username</h6>
                    <small>{selectedVendorEmployee.userName }</small>
                  </Media>
                </Media>
                <Media className='mt-2'>
                  <Avatar color='light-primary' className='rounded mr-1' icon={<Phone size={18} />} />
                  <Media body>
                    <h6 className='mb-0'>Phone Number</h6>
                    <small>{formatPhone(`${selectedVendorEmployee.countryCode} ${selectedVendorEmployee.phoneNumber}`)}</small>
                  </Media>
                </Media>
                <Media className='mt-2'>
                  <Avatar color='light-primary' className='rounded mr-1' icon={<Check size={18} />} />
                  <Media body>
                    <h6 className='mb-0'>Account Status</h6>
                    <small>{status[selectedVendorEmployee.status] !== undefined ? status[selectedVendorEmployee.status] : ''}</small>
                  </Media>
                </Media>
              </div>
            </Col>
            <Col xl='3' lg='12' className='mt-2 mt-xl-0'>
              <div className='user-info-wrapper'>
                <Media className='mt-2'>
                  <Avatar color='light-primary' className='rounded mr-1' icon={<Calendar size={18} />} />
                  <Media body>
                    <h6 className='mb-0'>Account Creation Date</h6>
                    <small>{formatDate(selectedVendorEmployee.createdAt, DATE_FORMATS.YMD_HMS)}</small>
                  </Media>
                </Media>
                <Media className='mt-2'>
                  <Avatar color='light-primary' className='rounded mr-1' icon={<Feather size={18} />} />
                  <Media body>
                    <h6 className='mb-0'>Role</h6>
                    <small>{roles[selectedVendorEmployee.role] !== undefined ? roles[selectedVendorEmployee.role] : ''}</small>
                  </Media>
                </Media>
              </div>
            </Col>
            <Col xl='2' lg='12' className='mt-2 mt-xl-0'>
              <div className='d-flex flex-wrap align-items-center my-50'>
                <Button.Ripple
                  block
                  color='primary'
                  tag={Link} to={`/vendors/${selectedVendorEmployee.vendorId}/employees/edit/${selectedVendorEmployee._id}`}>
                  Edit
                </Button.Ripple>
              </div>
              <div className='d-flex flex-wrap align-items-center my-50'>
                <Button.Ripple
                  block
                  outline
                  onClick={() => {
                    setAction('deactivate')
                    setModalTitle('Deactivate User')
                    setModalDesc('Are you sure you want to deactivate the user')
                    setDisabledModal(true)
                  }}
                  color='danger'>
                  Deactivate
                </Button.Ripple>
              </div>
              <div className='d-flex flex-wrap align-items-center my-50'>
                <Button.Ripple
                  block
                  color='danger'
                  onClick={() => {
                    setAction('delete')
                    setModalTitle('Delete User')
                    setModalDesc('Are you sure you want to delete the user')
                    setDisabledModal(true)
                  }}
                  outline>
                  Delete
                </Button.Ripple>
              </div>
            </Col>
          </Row>
        </CardBody>
      </Card>
      <Modal
        isOpen={disabledModal}
        toggle={() => setDisabledModal(!disabledModal)}
      >
        <ModalHeader toggle={() => setDisabledModal(!disabledModal)}>{modalTitle}</ModalHeader>
        <ModalBody>
          {modalDesc}
        </ModalBody>
        <ModalFooter>
          <Button color='primary' onClick={handleConfirm}>
            Yes
          </Button>
          <Button color='primary' onClick={() => setDisabledModal(!disabledModal)} outline>
            No
          </Button>
        </ModalFooter>
      </Modal>
    </>
  )
}

export default EmployeeInfoCard
