// ** React Imports
import { useEffect } from 'react'
import { Link, useParams } from 'react-router-dom'

// ** Store & Actions
import { getVendorEmployee } from '@store/Vendors/actions'
import { useDispatch, useSelector } from 'react-redux'

// ** Reactstrap
import { Alert, Col, Row } from 'reactstrap'

// // ** Employee View Components
import EmployeeInfoCard from './EmployeeInfoCard'
import EmployeePersonalInfoCard from './EmployeePersonalInfoCard'
import PermissionsTable from './PermissionsTable'

// ** Custom Components
import UILoader from '@components/ui-loader'
import CarhootBreadCrumbs from '@components/breadcrumbs/CarhootBreadCrumbs'

// ** Styles
import '@styles/react/apps/app-users.scss'

const EmployeeView = () => {
  // ** Vars
  const { isLoading, selectedVendorEmployee } = useSelector(state => state.vendors),
    dispatch = useDispatch(),
    { id, vendorId } = useParams()

  // ** Get suer on mount
  useEffect(() => {
    dispatch(getVendorEmployee(id, vendorId))
  }, [dispatch])

  return selectedVendorEmployee !== null && selectedVendorEmployee !== undefined ? (
    <UILoader blocking={isLoading}>

      <CarhootBreadCrumbs
        breadCrumbTitle='Breadcrumbs'
        breadCrumbItems={
          [
            {
              link: `/vendors/view/${selectedVendorEmployee.vendorId}`,
              title: 'Vendor Employees'
            },
            {
              title: 'Employee profile'
            }
          ]
        }
      />

      <div className='app-user-view'>
        <Row>
          <Col xl='12'>
            <EmployeeInfoCard selectedVendorEmployee={selectedVendorEmployee} />
          </Col>
        </Row>
        <Row>
          <Col xl='8' lg='8' md='12'>
            <EmployeePersonalInfoCard selectedVendorEmployee={selectedVendorEmployee} />
          </Col>
          <Col xl='4' lg='4' md='12'>
          </Col>
        </Row>
        <Row>
          <Col xl='12' lg='12' md='12'>
            <PermissionsTable selectedVendorEmployee={selectedVendorEmployee} />
          </Col>
        </Row>
      </div>
    </UILoader>
  ) : (
    <Alert color='danger'>
      <h4 className='alert-heading'>Vendor employee not found</h4>
      <div className='alert-body'>
        Vendor Employee with id: {id} doesn't exist. Check list of all Vendor Employees: <Link to={`/vendors/view/${vendorId}`}>Vendor Employees List</Link>
      </div>
    </Alert>
  )
}


export default EmployeeView
